html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  scroll-behavior: smooth;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.bodyNight {
  background-color: #000;
  color: #FFFFFF !important;
}

.pNight {
  color: #FFFFFF !important;
}

.sun {
  color: #FDC93A;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-image: url(../img/bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 50vh;
  position: relative;
  z-index: 1;
}
@media (min-width: 1024px) {
  .header {
    background-position: top center;
    height: 100vh;
  }
}

.header__layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.3;
  z-index: -9999;
}

.header__menu {
  display: block;
  padding: 2rem 0;
}
.header__menu i {
  color: #FFFFFF;
  font-size: 4rem;
  cursor: pointer;
}

.header__flex {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  align-items: center;
  justify-content: flex-start;
}

.header__menu {
  grid-column: 3/4;
}

.flex__menu {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 2rem;
}

.menu__item {
  background-color: #FFFFFF;
  border-radius: 10rem;
  padding: 1rem 0.5rem;
  width: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  cursor: pointer;
}
.menu__item i {
  font-size: 2rem;
}

/* Overlay */
.overlay {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.9);
  overflow-x: hidden;
  transition: 0.5s;
}

.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.overlay a:hover, .overlay a:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
}

@media screen and (max-height: 450px) {
  .overlay a {
    font-size: 20px;
  }

  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}
.header__title {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 80%;
  text-transform: uppercase;
  color: #FFFFFF;
}
.header__title img {
  width: 85%;
}
@media (min-width: 1024px) {
  .header__title img {
    width: 55%;
  }
}
.header__title .header__title-name {
  text-transform: capitalize;
  font-size: 2.4rem;
  margin: 0;
  font-weight: bold;
}
@media (min-width: 1024px) {
  .header__title .header__title-name {
    font-size: 4rem;
  }
}
.header__title .header__title-pro {
  font-weight: 300;
  text-transform: capitalize;
  margin: 0;
  font-style: oblique;
  font-size: 2rem;
}
@media (min-width: 1024px) {
  .header__title .header__title-pro {
    font-size: 3rem;
  }
}

.overlay {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  background-color: rgba(85, 63, 111, 0.9);
  overflow-x: hidden;
  transition: 0.5s;
}
.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #FFFFFF;
  display: block;
  transition: 0.3s;
}
.overlay a:hover, .overlay a:focus {
  color: #f5f5f5;
}
.overlay .closebtn {
  position: absolute;
  top: 20px;
  left: 48%;
  font-size: 60px;
}

.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

@media screen and (max-height: 450px) {
  .overlay a {
    font-size: 20px;
  }

  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}
.aboutme__head {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5rem 0;
  gap: 1rem;
}
.aboutme__head img {
  width: 5rem;
}

.aboutme__title {
  color: #FDC93A;
  font-weight: bold;
  font-size: 4rem;
}
@media (min-width: 1024px) {
  .aboutme__title {
    font-size: 5rem;
  }
}

.aboutme__subtitle {
  color: #FDC93A;
  font-weight: bold;
  font-size: 2.5rem;
  text-align: center;
  margin: 5rem 0;
}
@media (min-width: 1024px) {
  .aboutme__subtitle {
    font-size: 3.5rem;
  }
}

.aboutme__body {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 1024px) {
  .aboutme__body {
    flex-direction: row;
  }
}

.aboutme__picture {
  overflow: hidden;
  width: 20rem;
  border-radius: 50%;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .aboutme__picture {
    width: 30rem;
  }
}

.aboutme__text {
  max-width: 90%;
  width: 90%;
  margin: 0 auto;
}

.aboutme__flex {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .aboutme__flex {
    display: flex;
    justify-content: center;
  }
}
@media (min-width: 1024px) {
  .aboutme__flex {
    justify-content: flex-start;
  }
}

.aboutme__languages {
  font-weight: bold;
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 1024px) {
  .aboutme__languages {
    text-align: left;
  }
}

.language {
  display: flex;
  gap: 1rem;
}

.language__img {
  background-color: #FFFFFF;
  border: 1px solid #f5f5f5;
  border-radius: 5rem;
  padding: 1.2rem;
  width: 7rem;
  height: 7rem;
  object-fit: cover;
}

.language__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.language__name {
  display: block;
  margin: 0;
  padding: 0;
  font-weight: bold;
}

.language__lvl {
  display: block;
  margin: 0;
  padding: 0;
  font-style: italic;
  font-weight: lighter;
}

/*Cualidades*/
.grid__qualities {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  margin: 0 2rem;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .grid__qualities {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1200px) {
  .grid__qualities {
    grid-template-columns: repeat(6, 1fr);
  }
}

.quality__item {
  border: 1px solid #e0e0e0;
  padding: 1.2rem;
  border-radius: 50%;
  text-align: center;
  font-size: 3rem;
  text-align: center;
  position: relative;
  width: 10rem;
  margin: 0 auto;
  -webkit-box-shadow: 0px 0px 21px 0px rgba(0, 0, 0, 0.03);
  -moz-box-shadow: 0px 0px 21px 0px rgba(0, 0, 0, 0.03);
  box-shadow: 0px 0px 21px 0px rgba(0, 0, 0, 0.03);
}
.quality__item i {
  color: #FDC93A;
}
@media (min-width: 768px) {
  .quality__item {
    padding: 2rem;
    font-size: 4.5rem;
    width: 15rem;
  }
}
@media (min-width: 1024px) {
  .quality__item {
    padding: 3.6rem;
    font-size: 5rem;
    width: 17rem;
  }
}
@media (min-width: 1200px) {
  .quality__item {
    padding: 3.6rem;
    font-size: 5rem;
    width: 20rem;
  }
}

.quality__text {
  font-size: 1rem;
  text-align: center;
  font-weight: bold;
}
@media (min-width: 768px) {
  .quality__text {
    font-size: 1.5rem;
  }
}

/*Intereses*/
.grid__interests {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
@media (min-width: 768px) {
  .grid__interests {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1024px) {
  .grid__interests {
    grid-template-columns: repeat(5, 1fr);
  }
}

.interest__item {
  position: relative;
  width: 100%;
  margin: 0 auto;
}

.interest__img {
  display: block;
  width: 12rem;
  height: 12rem;
  border-radius: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .interest__img {
    width: 15rem;
    height: 15rem;
  }
}
@media (min-width: 1024px) {
  .interest__img {
    width: 17rem;
    height: 17rem;
  }
}

.overlay__img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  transition: 0.5s ease;
  background-color: #FDC93A;
  border-radius: 50%;
  width: 12rem;
  height: 12rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .overlay__img {
    width: 15rem;
    height: 15rem;
  }
}
@media (min-width: 1024px) {
  .overlay__img {
    width: 17rem;
    height: 17rem;
  }
}

.interest__item:hover .overlay__img {
  opacity: 1;
  cursor: pointer;
}

.text {
  color: #FFFFFF;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.banner__studiesexp {
  margin: 10rem 0;
  background-image: url(../img/5.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  height: 20vh;
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 1;
}
@media (min-width: 768px) {
  .banner__studiesexp {
    height: 30vh;
  }
}

.studiesexp__layer {
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.3;
  z-index: -9999;
}

.studiesexp__title {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 3rem;
  text-align: center;
}
@media (min-width: 1024px) {
  .studiesexp__title {
    font-size: 5rem;
  }
}

.studiesexp__parrafo {
  margin-top: 1rem;
  font-size: 1.5rem;
  max-width: 120rem;
  margin: 0 auto;
}

/*Estudios y Experiencias */
.stuexp__grid {
  display: grid;
  gap: 2rem;
  margin-bottom: 10rem;
}
@media (min-width: 768px) {
  .stuexp__grid {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}

.stupexp__studies,
.stupexp__exp {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 1024px) {
  .stupexp__studies,
.stupexp__exp {
    height: 30rem;
  }
}

.stuexp__item {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 2rem;
}

.stuexp__title {
  color: #949393;
  font-weight: bold;
  font-style: italic;
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .stuexp__title {
    font-size: 3rem;
  }
}

.stuexp__img {
  background-color: #FFFFFF;
  padding: 1rem;
  border-radius: 50%;
  text-align: center;
  border: 1px solid #e0e0e0;
  font-size: 3rem;
  color: #FDC93A;
  width: 10rem;
  height: 10rem;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-box-shadow: 0px 0px 21px 0px rgba(0, 0, 0, 0.03);
  -moz-box-shadow: 0px 0px 21px 0px rgba(0, 0, 0, 0.03);
  box-shadow: 0px 0px 21px 0px rgba(0, 0, 0, 0.03);
}
@media (min-width: 1024px) {
  .stuexp__img {
    width: 12rem;
    height: 12rem;
    font-size: 4rem;
  }
}

.stuexp__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.stuexp__text p {
  margin: 0;
  padding: 0;
}

.stuexp__name {
  font-weight: bold;
}

.stuexp__degree,
.stuexp__year {
  font-weight: lighter;
  font-style: italic;
}

.banner__skills {
  margin: 10rem 0;
  background-image: url(../img/d.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  height: 110rem;
  position: relative;
  z-index: 1;
}
@media (min-width: 768px) {
  .banner__skills {
    height: 75rem;
  }
}

.skills__layer {
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.3;
  z-index: -9999;
}

.studiesexp__head {
  padding: 10rem 5rem 2rem 5rem;
  display: flex;
  flex-direction: column;
  color: #FFFFFF;
}

.skills__title {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 4rem;
}
@media (min-width: 1024px) {
  .skills__title {
    font-size: 5rem;
  }
}

.skill__text {
  color: #000;
  line-height: 1.2;
  width: 70px;
  text-align: center;
  font-size: 1.25rem;
  word-break: break-all;
  font-weight: 400;
}

.studiesexp__parrafo {
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .studiesexp__parrafo {
    font-size: 2rem;
  }
}

.software__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  gap: 5rem;
}
@media (min-width: 768px) {
  .software__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .software__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

/*Progress Circle*/
.progress {
  width: 150px;
  height: 150px;
  float: left;
  line-height: 150px;
  background: none;
  margin: 0 auto;
  box-shadow: none;
  position: relative;
}

.progress:after {
  content: "";
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 12px solid #fff;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #FFFFFF;
}

.progress > span {
  width: 50%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  top: 0;
  z-index: 1;
}

.progress .progress-left {
  left: 0;
}

.progress .progress-bar {
  width: 100%;
  height: 100%;
  background: none;
  border-width: 8px;
  border-style: solid;
  position: absolute;
  top: 0;
}

.progress .progress-left .progress-bar {
  left: 100%;
  border-top-right-radius: 80px;
  border-bottom-right-radius: 80px;
  border-left: 0;
  -webkit-transform-origin: center left;
  transform-origin: center left;
}

.progress .progress-right {
  right: 0;
}

.progress .progress-right .progress-bar {
  left: -100%;
  border-top-left-radius: 80px;
  border-bottom-left-radius: 80px;
  border-right: 0;
  -webkit-transform-origin: center right;
  transform-origin: center right;
  animation: loading-1 1.8s linear forwards;
}

.progress .progress-value {
  width: 90%;
  height: 90%;
  border-radius: 50%;
  background: #000;
  font-size: 24px;
  color: #fff;
  line-height: 135px;
  text-align: center;
  position: absolute;
  top: 5%;
  left: 5%;
}

.progress.blue .progress-bar {
  border-color: #FDC93A;
}

.progress.blue .progress-left .progress-bar {
  animation: loading-2 1.5s linear forwards 1.8s;
}

.progress.yellow .progress-bar {
  border-color: #fdba04;
}

.progress.yellow .progress-right .progress-bar {
  animation: loading-3 1.8s linear forwards;
}

.progress.yellow .progress-left .progress-bar {
  animation: none;
}

@keyframes loading-1 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
  }
}
@keyframes loading-2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(144deg);
    transform: rotate(144deg);
  }
}
@keyframes loading-3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
  }
}
