@use '../base/variables' as v;
@use '../base/mixins' as m;

.overlay {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 9999;
    top: 0;
    left:0;
    background-color: rgba($color: v.$primario, $alpha: 0.9);
    overflow-x: hidden;
    transition: 0.5s;

    a{
        padding: 8px;
        text-decoration: none;
        font-size: 36px;
        color: v.$blanco;
        display: block;
        transition: 0.3s;

        &:hover,
        &:focus{
            color: v.$gris;
        }
    }

    .closebtn {
        position: absolute;
        top: 20px;
        left: 48%;
        font-size: 60px;
    }
}
 
.overlay-content {
    position: relative;
    top: 25%;
    width: 100%;
    text-align: center;
    margin-top: 30px;
}
  
  
@media screen and (max-height: 450px) {
    .overlay a {font-size: 20px}
    .overlay .closebtn {
        font-size: 40px;
        top: 15px;
        right: 35px;
    }
}